'use strict';

import angular from 'angular';
import 'angular-resource';
import urlService from 'common/services/url.service';

export var userModule = angular.module('user.service.js', [
  'ngResource',
  urlService.name
]);

userModule.service('User', function ($resource, URL) {
    return $resource( URL.apiBase + '/api/users/:id/:controller', {
      id: '@_id'
    },
    {
      // changePassword: {
      //   method: 'PUT',
      //   params: {
      //     controller:'password'
      //   }
      // },
      get: {
        method: 'GET',
        params: {
          id:'me'
        }
      },
      save: {
        method: 'POST'
      }
    });
  });

export default userModule;
